body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html, body {
  height: 100%;
}

body {
  display: flex;
  flex-direction: column;
}

#root {
  flex: 1 0 auto;
  display: flex;
  flex-direction: column;
  height: 100%;
}

.App {
  text-align: center;
  height: 100;
  display: flex;
  flex-direction: column;
  flex: 1 0 auto;
}

.App-intro {
  font-size: large;
}

.Main-Container {
  padding: 1.5rem;
  background: #00e9de;
}

.Current-Playlist {
  text-align: center;
}

.Search-Bar-Wrapper {
  flex: 1 0 auto;
  background: #222;
  padding-top: 1.5rem;
}

@media only screen and (min-width: 800px) {
  .Player-View-Container {
    display: inline-flex;
  }
  .Current-Playlist {
    padding-right: 2rem;
    text-align: left;
  }
}

@media only screen and (max-width: 800px) {
  .Current-Playlist {
    text-align: left;
  }
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.App-logo {
    -webkit-animation: App-logo-spin infinite 15s linear;
            animation: App-logo-spin infinite 15s linear;
    height: 4rem;
}

.App-header {
    background-color: #222;
    height: 15rem;
    padding: 1.5rem;
    padding-top: 2rem;
    color: white;
    font-family: 'Roboto', sans-serif;
}

.song-heading, .welcome-heading {
    margin-top: 1.5rem;
    font-size: 1.5rem;
}

.username-field {
    color: #00e9de
}

@media only screen and (max-width: 800px) {
    .App-logo {
       display: none;
    }

    .song-heading {
        margin-top: 0;
        font-size: 1.3rem;
    }

    .welcome-heading {  
        font-size: 1.2rem;
    }
    
    .App-header {
        height: 9rem;
    }
}
.suggestion-list-item {
    list-style: none;
    width: 100%;
    display: flex;
    flex-direction: row;
}

.suggestion-thumbnail {
    height: 8rem;
    width: 11.5rem;
}

.suggestion-description {
    color: white;
    font-family: 'Roboto', sans-serif;
    padding-left: 1.5rem;
    padding-right: 1.5rem;
    display: flex;
    flex-direction: column; 
    text-align: left;
    width: 100%;
    white-space: nowrap; 
    overflow: hidden;
    text-overflow: ellipsis; 
}

.suggestion-title {
    font-size: 1.5rem;
    margin: 0;
}

.video-duration, .video-channel {
    font-size: 1.3rem;
    margin-top: 1.2rem;
}

.video-suggestion-seperator {
    margin-top: 1.1rem;
    margin-bottom: 1.1rem; 
}

.video-suggestion-list {
    padding: 0;
    padding-right: 1rem;
    height: 75rem;
    overflow: auto;
}

.video-suggestion-list::-webkit-scrollbar-track {
	-webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
	background-color: #F5F5F5;
}

.video-suggestion-list::-webkit-scrollbar {
	width: 6px;
	background-color: #F5F5F5;
}

.video-suggestion-list::-webkit-scrollbar-thumb {
	background-color: #000000;
}

.add-video-button {
    margin-top: 0rem;
    border-radius: 0;
    background: #222;
    border: 0;
    margin-right: 1rem; 
}

.add-video-button:hover, .add-video-button:focus {
    background: #222;
    border: 0;
    border: white;
    color: #00e9de;
}

/* Icons */
.music-icon {
    margin-right: 1.2rem;
}

.youtube-icon, .stopwatch-icon {
    margin-right: 1.3rem;
}

.plus-icon {
    font-size: 2.5rem;
}

@media only screen and (max-width: 800px) {
    .suggestion-thumbnail {
        height: 5rem;
        width: 6.5rem;
    }

    .stopwatch-icon, .youtube-icon, .music-icon {
        display: none;
    }
    
    .plus-icon {
        font-size: 1.5rem;
    }

    .suggestion-description {
        padding-left: 0.8rem;
        padding-right: 0;
    }

    .suggestion-title {
        font-size: 1.2rem;
    }

    .add-video-button {
        border: 0;
        margin-right: 0; 
    }

    .video-duration, .video-channel {
        font-size: 1.1rem;
        margin-top: 0.4rem;
    }

    .video-suggestion-list {
        height: 50rem;
    }
}
.video-search-section {
    margin: auto;
    max-width: 105rem;
}

.search-bar-wrapper {
    padding: 0.8rem;
}

.search-bar {
    height: 1.6rem;
    padding: 1.6rem;
    border-radius: 0.3rem;
}

.video-suggestions {
    padding: 1rem;
    padding-top: 0; 
    background: #222;
}

.search-video-prompt-container {
    color: white;
    font-family: 'Roboto', sans-serif;
}

.search-video-prompt-suffix {
    color: #00e9de;
    font-family: 'Roboto', sans-serif;
}

.head-phone-icon {
    margin-top: 2rem;
    font-size: 8.5rem;
}

@media only screen and (max-width: 800px) {
    .search-bar {
        height: 1rem;
        padding: 1.3rem;
        border-radius: 0.2rem;
    }

    .head-phone-icon {
        font-size: 4.5rem;
        margin-top: 1.2rem; 
    }
    
    .search-video-prompt-prefix, .search-video-prompt-suffix {
        font-size: 1.5rem;
    }

    .video-search-section {
        padding: 0.5rem;
        padding-top: 0;
    }
}
.suggested-user {
    color: #00e9de;
}

.control-buttons {
    margin-top: 1rem;

}

.skip-video-button{
    margin-right: 1.5rem;
    padding: 0.5rem;
    background: none;
    border: solid #00e9de 1px;
    color: #00e9de;
}

.skip-video-button:hover, .skip-video-button:focus {
    background: none;
    border: solid #00e9de 1px;
    color: white;
}

.volume-buttons, .mute-button {
    background: none;
    border: solid #00e9de 1px;
    color: #00e9de;
}

.volume-buttons:hover, .volume-buttons:focus, .mute-button:hover, .mute-button:focus {
    background: none;
    border: solid #00e9de 1px;
    color: white;
}

.mute-button {
    border-radius: 3px 0px 0px 3px;
}

.volume-buttons {
    border-radius: 0;
    width: 4.5rem;
}

.skip-icon {
    margin-left: 0.2rem;
}
.Youtube-Player-Wrapper {
    margin-bottom: 1.5rem;
    margin-left: 1.5rem;
    padding: 1.5rem;
    background: rgb(0, 0, 0, 0.7);
}

.Youtube-Player {
    pointer-events: none;
    padding-bottom: 0.5rem;
}

.Youtube-Player-Debug {
    padding-bottom: 0.5rem;
}

.Video-Banner-Title {
    font-size: 1.5rem;
    color: white;
    margin-top: 0;
    margin-bottom: 0;
    font-family: 'Roboto', sans-serif;
}

@media only screen and (max-width: 800px) {
    .Youtube-Player {
        height: 0;
        width: 0;
    }
    .Youtube-Player-Debug {
        height: 0;
        width: 0;
    }

    .Youtube-Player-Wrapper {
        padding: 1.5rem;
        margin-left: 0; 
        background: rgb(0, 0, 0, 0.7);
    }

    .Video-Banner-Title {
        font-size: 1.3rem;
        margin-top: -2.2rem;
    }
}

.Playlist-Section {
    padding: 1rem;
    background: #222222;
    font-family: 'Roboto', sans-serif;
}

.Playlist-Header {
    color: white;
    text-align: center;
    margin-top: 0.5rem;
}

.Video-Playlist {
    list-style: none;
    padding: 0;
    padding-bottom: 0;
    height: 25rem;
    overflow: auto;
    width: 45rem;
}

.Empty-Video-Background {
    text-align: center;
    padding-top: 8rem;
    color: white;
    width: inherit;
    height: inherit;
}

/* Prettier scrollbar, courtesy of: https://codepen.io/devstreak/pen/dMYgeO */
.Video-Playlist::-webkit-scrollbar-track {
	-webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
	background-color: #F5F5F5;
}

.Video-Playlist::-webkit-scrollbar {
	width: 6px;
	background-color: #F5F5F5;
}

.Video-Playlist::-webkit-scrollbar-thumb {
	background-color: #000000;
}

.Music-Icon {
    margin-right: 0.8rem; 
}

.Playlist-Item-Wrapper:first-child {
    color: #00e9de;
    font-weight: 500;
}

.Playlist-Item-Wrapper {
    color: white;
}

.Playlist-Item {
    padding: 0.5rem;
    padding-top: 1rem;
    padding-bottom: 1rem;
    white-space: nowrap; 
    overflow: hidden;
    text-overflow: ellipsis;
}

.Playlist-Item:hover {
    background: rgb(0, 0, 0, 0.7);
}

.seperator {
    margin: 0;
    background: #a59c9c;
}

.No-Video-Text {
    margin: 0;
    font-size: 1.6rem;
}

.Pause-Icon {
    font-size: 5rem;
    padding-bottom: 1.5rem;
}


@media only screen and (max-width: 800px) {
    .Video-Playlist {
        width: inherit;
        height: 22rem;
    }

    .Playlist-Header {
        font-size: 1.5rem;
        margin-top: 0;
    }

    .No-Video-Text {
        font-size: 1.4rem;
    }

    .Pause-Icon {
        font-size: 3.5rem;
        padding-bottom: 1.5rem;
    }
    
    .Playlist-Item {
        font-size: 1.2rem;
    }
}
/* Overide bootstrap navbar */

.navbar {
    border-radius: 0;
    border-bottom: 1px solid #a59c9c;
    margin-bottom: 0; 
}

.navbar > .container {
    width: 85%;
    padding-left: 1.5rem;
    padding-right: 1.5rem;
}

.navbar-inverse .navbar-brand {
    font-weight: 550;
    color: #00e9de;
}

#Users-Nav-Dropdown {
    color: white;
}

.User-Icon { 
    padding: 0.4rem;
    border: 1.5px solid;
    border-radius: 1.5rem;
}

.Username-List-Item {
    padding-left: 1rem;
}
